export default {
  props: {
    placeholder: String,
    maxlength: Number,
    type: String,
    value: String | Number | undefined,
    inputStyle: Object
  },
  data () {
    return {
      input: null,
      myValue: this.value
    }
  },
  watch: {
    value (v) {
      this.myValue = v
    },
    myValue (v) {
      this.$emit('input', v)
    }
  },
  methods: {
    clearText () {
      this.myValue = ''
    }
  },
  mounted () {
    this.input = this.$refs.input
  }
}



